// resizeCanavs.js

import alfrid, { GL } from 'alfrid';

const resize = (w, h, containerWidth, containerHeight, isAR) => {

	// fix with/height at integration
	const parentElement = document.querySelector('.section-landing__container-webgl');
	if (parentElement) {
		// integration
		containerWidth |= parentElement.offsetWidth;
      	containerHeight |= parentElement.offsetHeight;
	}
	else {
		// standalone fallback
		containerWidth |= window.innerWidth;
		containerHeight |= window.innerHeight;
	}
	
	w           = w || containerWidth;
	h           = h || containerHeight;
	
	const devicePixelRatio = isAR ? 1 : Math.min(window.devicePixelRatio, 1.5);
	GL.setSize(w * devicePixelRatio, h * devicePixelRatio);
	let tw      = Math.min(w, containerWidth);
	let th      = Math.min(h, containerHeight);
	
	const sx    = containerWidth / w;
	const sy    = containerHeight / h;		
	const scale = Math.min(sx, sy);
	tw          = w * scale;
	th          = h * scale;

	GL.canvas.style.width  = `${tw}px`;
	GL.canvas.style.height = `${th}px`;
}


export { resize };