import { getCameraDistanceFitX, getScales } from 'utils';

import Config from '../Config';
import { GL } from 'alfrid';
import { OrbitalControlTween } from 'helpers';
import { StateDefault } from './StateDefault';
import gsap from 'gsap';

export class StateIntroToHome extends StateDefault {
    begin() {
    // return;
        this.product = this.scene._vProduct;
        this.controls = this.scene.orbitalControl;

        const duration = Config.fast ? 1 : 2;

        gsap.to(this.product, duration, {
            transitionIntroToHomeVal: 0,
            ambientValue: 0.4,
            rotationX: 0,
            rotationZ: 0,
            darkness: 0,
            ease: 'sine.inout',
            y: 0,
        });
        gsap.to(this.product.posCamera, duration, {
            0: -4.5,
            1: -0.2,
            2: -2,
            ease: 'sine.inout', 
        });

        this.controls.lockZoom(false);

        const isMobile = GL.isMobile;
        const widthToFit = isMobile ? window.innerWidth : window.innerWidth / 2.8;
        const radius = getCameraDistanceFitX(widthToFit, (Config.productDimensions.x * Config.productScale), (Config.productDimensions.z * Config.productScale) * (isMobile ? 1 : 0), this.scene.camera, this.controls);

        const { scaleX, scaleY } = getScales(this.scene.camera, this.controls, 1, 1, 0, radius);

        const offsetX = isMobile ? 0 : -scaleX / 8;
        const offsetY = isMobile ? -scaleY / 12 : scaleY / 6;

        OrbitalControlTween.tween({
            duration,
            centerX: offsetX, centerY: offsetY, centerZ: 0,
            offsetX, offsetY, offsetZ: 0,
            rx: 0,
            ry: 0,
            radius,
            ease: 'sine.inout',
            onComplete: () => {
                this.scene.statesSystem.set('home');
            }
        });

        if (isMobile) {
            OrbitalControlTween.tween({
                orbital: this.scene.controlsProduct,
                duration,
                rx: 0.088,
                ease: 'sine.inout',
            });
        }

        // const o = { y: 0 }
        // gui.add(o, 'y', -2, 2).step(0.01).onChange(()=>{
        //   this.controls.positionOffset[1] = o.y;
        //   this.controls.center[1] = o.y;
        //   // this.product.y = o.y;
        // })
    }

    update() {
    // this.scene.controls
    }

    end() {

    }
}