import { WebXRButton } from 'libs/webxr/WebXRButton'
import { onSessionError } from 'signals';
// import WebXRPolyfill from 'libs/webxr/polyfill';

let xrButton;
let scene;
let container;

function addXRButton () {
  xrButton = new WebXRButton({
    onRequestSession: onRequestSession,
    // onErrorSession: onErrorSession,
    onEndSession: onEndSession,
    textEnterXRTitle: "EXPLORE",
    textXRNotFoundTitle: "AR NOT FOUND",
    textExitXRTitle: "EXIT AR",
  });

  
  if (navigator.xr) {
    navigator.xr.isSessionSupported('immersive-ar').then((supported) => {
      if(supported) {
        xrButton.enabled = supported;
        xrButton.domElement.className = 'button button-pill section-landing__cta';
        container.innerHTML = '';
        container.appendChild(xrButton.domElement);
      }
    });
  }
}

function onErrorSession(err) {
  // onSessionError.dispatch(err);
}

function onRequestSession() {
  return navigator.xr.requestSession('immersive-ar', {requiredFeatures: ['local', 'hit-test']})
    .then((session) => {
      session.isImmersive = true;
      xrButton.setSession(session);
      onSessionStarted(session);
    })
}

// not really used, but will need to call session.end from a webgl button
function onEndSession(session) {
  session.end();
}

function onSessionEnded(session) {
  xrButton.setSession(null);
}

function onSessionStarted(session) {
  session.addEventListener('end', onSessionEnded);
  scene.addSession(session);
}

const createARLayer = (mScene, containerButton) => {
  container = containerButton || document.querySelector('header');
  scene = mScene;
  addXRButton();

  return xrButton;
}

export { createARLayer };