// ViewCube.js

import alfrid, { GL } from 'alfrid';

import fs from 'shaders/intro-fade.frag';

export class ViewIntroMask extends alfrid.View {
  constructor(scene) {
    super(alfrid.ShaderLibs.bigTriangleVert, fs);
    this.scene = scene;

    this.active = true;
    this.alpha = 1;
    this.mesh = alfrid.Geom.bigTriangle();
  }

  start() {
    this.active = true;
  }

  stop() {
    this.active = false;
  }

  
  render() {
    if (!this.active) return;

    this.shader.bind();
    this.shader.uniform('uAlpha', 'float', this.alpha)
    GL.draw(this.mesh);
  }
}
