import { GL } from 'alfrid';
import { vec3 } from 'gl-matrix';

const tempVec3 = vec3.create();
// return this.getXY(pos, this.scene.camera._matrix, this.scene.camera.projection, GL.width, GL.height);

const get2dPos = (pos) => {
  let containerWidth = window.innerWidth;
  let containerHeight = window.innerHeight;
  // fix with/height at integration
  const parentElement = document.querySelector('.section-landing__container-webgl');
  if (parentElement) {
    // integration
    containerWidth = parentElement.offsetWidth;
    containerHeight = parentElement.offsetHeight;
  }

  vec3.transformMat4(tempVec3, pos, GL.camera._matrix);
  vec3.transformMat4(tempVec3, tempVec3, GL.camera.projection);

  tempVec3[0] = containerWidth * (tempVec3[0] + 1.0)/2.0;
  tempVec3[1] = containerHeight * (1.0 - ((tempVec3[1] + 1.0) / 2.0));

  return tempVec3;
}

export { get2dPos };